        style="float: right; z-index: 3; width: 8px; height: 8px; background-color: red;"
        @click="settings.controls = !settings.controls"
      >

      </div>
      <table v-show="settings.controls">
        <tr>
          <td>background:</td>
          <td>
            R
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.r"
            >
          </td>
          <td>
            G
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.g"
            >
          </td>
          <td>
            B
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.bg.b"
            >
          </td>
          <td></td>
        </tr>
        <tr>
          <td>circles:</td>
          <td>
            R
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.r"
            >
          </td>
          <td>
            G
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.g"
            >
          </td>
          <td>
            B
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.b"
            >
          </td>
          <td></td>
        </tr>
        <tr>
          <td>&nbsp;</td>
          <td>
            MinSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.minSize"
            >
          </td>
          <td>
            MaxSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.maxSize"
            >
          </td>
          <td>&nbsp;</td>
          <td>&nbsp;</td>
        </tr>
        <tr>
          <td>other:</td>
          <td>
            cellWidth
            <input
              min="10"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellWidth"
            >
          </td>
          <td>
            cellHeight
            <input
              min="10"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellHeight"
            >
          </td>
          <td>
            Speed
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.speed"
            >
          </td>
          <td>
            FPS: {{stats.fps}}
          </td>
        </tr>
      </table>
    </div>

    <audio width="200" height="200" id="colorqsuad" crossorigin="anonymous" loop autoplay controls>
      <source src="colorqsuad.mp3" type="audio/mp3">
      You need an MP3-supporting browser.
    </audio>

    <div id="viz">

    </div>

  </div>
</template>

<script>
import * as PIXI from 'pixi.js'

export default {
  name: 'app',

  data () {
    return {
      settings: {
        controls: true,
        speed: 14,
        bg: {
          r: 0,
          g: 0,
          b: 15
        },
        grid: {
          cellWidth: 30,
          cellHeight: 30
        },
        circle: {
          r: 0,
          g: 53,
          b: 72,
          maxSize: 7,
          minSize: 0
        }
      },
      grid: {
        cols: 0,
        rows: 0,
        width: 0,
        height: 0
      },
      layout: {
        width: window.innerWidth,
        height: window.innerHeight
      },
      stats: {
        fps: null,
        t1: null,
        t2: null
      },
      audio: {
        bufferLength: null,
        data: []
      }
    }
  },

  created () {
    this.circles = []
  },

  mounted () {
    this.stats.t1 = (performance || Date).now()
    this.stats.frames = 0

    window.PIXI = PIXI
    this.setup()
    this.setupAudio()
  },

  watch: {
    settings: {
      handler () {
        console.log('settings change')
        this.debounceResize()
      },
      deep: true
    }
  },

  methods: {

    setup () {
      console.log('setup: ', this.layout.width, this.layout.height)

      // app creation
      this.app = new PIXI.Application({
        antialias: true,
        width: this.layout.width,
        height: this.layout.height,
        // backgroundColor: 0x1099bb,
        backgroundColor: this.rgbToHex(this.settings.bg.r, this.settings.bg.g, this.settings.bg.b),
        resolution: window.devicePixelRatio || 1
      })
      document.body.appendChild(this.app.view)
      console.log('app: ', this.app)
      window.app = this.app

      // container creation
      // this.container = new PIXI.Container()
      // this.app.stage.addChild(this.container)
      // console.log('container: ', this.container)

      // texture creation
      this.svg = PIXI.Texture.from('circle.svg')

      // boot after a while to allow resources loading
      requestAnimationFrame(() => {
        console.log('ticker: ', this.app.ticker)
        this.app.ticker.speed = this.settings.speed * 100

        this.setupGrid(this.layout.width, this.layout.height)

        this.app.ticker.add((delta) => {
          // use delta to create frame-independent transform
          // eg. container.rotation -= 0.01 * delta
          this.animate(delta)
        })
        window.addEventListener('resize', this.debounceResize)
      })
    },

    destroy () {
      window.removeEventListener('resize', this.debounceResize)
      if (this.app && typeof this.app.destroy === 'function') {
        console.log('app destroy')
        this.app.destroy(true, true)
        this.container = null
        this.circles = []
        this.svg = null
        this.app = null
        window.app = null
      }
    },

    circle (x, y, size) {
      let circle = new PIXI.Sprite(this.svg)
      // circle.tint = Math.random() * 0xFFFFFF
      circle.tint = this.rgbToHex(this.settings.circle.r, this.settings.circle.g, this.settings.circle.b)
      circle.anchor.set(0.5)
      circle.x = x
      circle.y = y
      // circle.size = 10
      circle.scale.x = size / 100
      circle.scale.y = size / 100

      circle.size = size
      circle.grow = size > this.settings.circle.minSize && size < this.settings.circle.maxSize ? Math.random() < 0.5 : !size

      // console.log('circle init: ', circle.grow, circle)
      return circle
    },

    animate (delta) {
      // console.log('animate: ', this.app.ticker.maxFPS, this.app.ticker.FPS)

      this.stats.t2 = (performance || Date).now()
      if (this.stats.t2 >= this.stats.t1 + 1000) {
        this.stats.fps = Math.round(this.app.ticker.FPS)
        this.stats.t1 = this.stats.t2
      }

      this.app.stage.children.forEach((circle) => {
        // circle.scale.x += 0.001
        const size = circle.scale.x

        const hasSize = circle.scale.x > 0
        circle.grow = size > this.settings.circle.minSize / 100 && size < this.settings.circle.maxSize / 100 ? circle.grow : !hasSize

        const step = circle.grow ? 0.001 : -0.001

        circle.scale.x = ((circle.scale.x * 1000 + step * delta) / 1000)
        circle.scale.y = ((circle.scale.y * 1000 + step * delta) / 1000)



        const audioStep = this.audio.data[circle.col] || 0 / 2000

        if (!audioStep || !circle.lastAudio || circle.lastAudio < this.audio.data[circle.col]) {
          circle.tint = this.rgbToHex(this.settings.circle.r, this.settings.circle.g, this.settings.circle.b)
        } else {
          circle.tint = this.colorLuminance(circle.tint, 0.2)
        }

        circle.lastAudio = this.audio.data[circle.col]
        // circle.scale.x = audioStep
        // circle.scale.y = audioStep
      })
    },

    resize () {
      console.log('resize')
      this.layout.width = window.innerWidth
      this.layout.height = window.innerHeight

      this.destroy()
      this.setup()
    },

    debounceResize () {
      window.clearTimeout(window.resize)
      window.resize = window.setTimeout(this.resize, 100)
    },

    setupGrid (width, height) {
      console.log('setupGrid: ', width, height)
      this.circles = []
      this.grid.width = width
      this.grid.height = height

      this.grid.cols = Math.floor(width / this.settings.grid.cellWidth)
      this.grid.rows = Math.floor(height / this.settings.grid.cellHeight)

      for (let i = 0; i < this.grid.cols; i++) {
        for (let j = 0; j < this.grid.rows; j++) {
          const size = Math.floor(Math.random() * (this.settings.circle.maxSize - this.settings.circle.minSize + 1)) + this.settings.circle.minSize

          const x = i * this.settings.grid.cellWidth + this.settings.grid.cellWidth / 2
          const y = j * this.settings.grid.cellHeight + this.settings.grid.cellHeight / 2

          const circle = this.circle(x, y, size)
          circle.col = i
          circle.row = j
          this.app.stage.addChild(circle)
        }
      }
    },

    rgbToHex (r, g, b) {
      return '0x' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)
    },

    setupAudio () {
      // Create audio context
      var AUDIO = new (window.AudioContext || window.webkitAudioContext)()
      if (!AUDIO) {
        console.log('Web Audio API not supported :(')
        return
      }

      // Create and configure analyzer node and storage buffer
      var analyzer = AUDIO.createAnalyser()
      analyzer.fftSize = 128
      this.audio.bufferLength = analyzer.frequencyBinCount
      console.log('this.audio.bufferLength:', this.audio.bufferLength)
      this.audio.data = new Uint8Array(this.audio.bufferLength)

      // Cache HTML elements
      var vid = document.getElementById('colorqsuad')
      var vizArea = document.getElementById('viz')

      // Creates a set of divs with graded BG colors.
      // Returns it as an array of jQuery-wrapped elements for
      // later manipulation.
      const createSegments = (numSegments) => {
        console.log('numSegments', numSegments)
        var segCollection = []
        var colorSlice = Math.floor(255 / numSegments)

        for (var i = 0; i < numSegments; i++) {
          var s = document.createElement('div')
          s.classList.add('viz-seg')
          var g = colorSlice * i
          var r = 127 - (colorSlice * i)

          s.style.backgroundColor = 'rgba(' + r + ',' + g + ',255,0.1)'
          vizArea.appendChild(s)
          segCollection.push(s)
        }
        return segCollection
      }

      var $segs
      var rot = 0

      // Main update/render method.
      // Gets the current frequency data and transforms the div set based on it.
      // Used jQuery because I'm too lazy to look up my transform-prefix shiv.
      const update = () => {
        analyzer.getByteFrequencyData(this.audio.data)
        for (var i = 0; i < this.audio.bufferLength; i++) {
          // Scaled based on frequency, staggered rotation
          var segScale = this.audio.data[i] / 255
          var segRotate = rot + (i * 3)

          if (i === 0) {
            // console.log('DATAARRAY: ', this.audio.data[i], segScale, segRotate)
          }

          // $segs[i].css('transform', 'scale(' + (segScale) + ') rotate(' + segRotate + 'deg)')
          // $segs[i].style.transform = 'scale(' + (segScale) + ') rotate(' + segRotate + 'deg)'
        }
        if (++rot > 360) rot = 0
      }

      const init = () => {
        // Connect video to analyzer and analyzer to audio-out
        console.log('init')
        var source = AUDIO.createMediaElementSource(vid)
        source.connect(analyzer)
        analyzer.connect(AUDIO.destination)

        // $segs = createSegments(this.audio.bufferLength)
        loop()
      }

      // Main loop
      const loop = () => {
        requestAnimationFrame(loop)
        // vid.play()
        update()
      }

      // Kick it off when the video is playable
      vid.addEventListener('loadeddata', () => {
        init()

        /* Adding a 'Play' button so we can activate things */
        const playOverlay = document.createElement('div')
        playOverlay.style.position = 'fixed'
        playOverlay.style.backgroundColor = 'rgba(0,0,100,0.3)'
        playOverlay.style.top = '0'
        playOverlay.style.left = '0'
        playOverlay.style.right = '0'
        playOverlay.style.bottom = '0'
        playOverlay.style.display = 'flex'
        playOverlay.style.alignItems = 'center'
        playOverlay.style.justifyContent = 'center'
        const playBut = document.createElement('button')
        playBut.textContent = 'Click to play'
        playBut.style.padding = '1em'
        playOverlay.appendChild(playBut)
        playBut.addEventListener('click', (e) => {
          playOverlay.remove()
          vid.play()
        })
        document.body.appendChild(playOverlay)
        /* End */
      }, false)
    },

    // lum range [-1, 1]
    colorLuminance (hex, lum) {
      // validate hex string
      hex = String(hex).replace(/^(0x)/gi, '')
      if (hex.length < 6) {
        hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2]
      }
      lum = lum || 0

      // convert to decimal and change luminosity
      var rgb = '0x'; var c; var i
      for (i = 0; i < 3; i++) {
        c = parseInt(hex.substr(i * 2, 2), 16)
        c = Math.round(Math.min(Math.max(0, c + (c * lum)), 255)).toString(16)
        rgb += ('00' + c).substr(c.length)
      }

      // console.log(rgb)

      return rgb
    }
  }
}
</script>

